import Vue from "vue";
import VueRouter from "vue-router";

// Nprogress
import NProgress from "nprogress";
import "nprogress/nprogress.css";

// Pages
import Home from "../views/Home.vue";
import About from "../views/About.vue";
import Portfolio from "../views/Portfolio.vue";
import AnaFayez from "../views/works/AnaFayez.vue";
import DrsWorld from "../views/works/DrsWorld.vue";
import Moaaen from "../views/works/Moaaen.vue";
import CerStars from "../views/works/CerStars.vue";
import Ketabak from "../views/works/Ketabak.vue";
import Medad from "../views/works/Medad.vue";
import Merjan from "../views/works/Merjan.vue";
import NTS from "../views/works/NTS.vue";
import Salam from "../views/works/Salam.vue";
import Takkat from "../views/works/Takkat.vue";
import MedadApp from "../views/works/MedadApp.vue";
import DrsWorldApp from "../views/works/DrsWorldApp.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/About",
    name: "About",
    component: About
  },
  {
    path: "/Portfolio",
    name: "Portfolio",
    component: Portfolio
  },
  {
    path: "/Portfolio/AnaFayez",
    name: "AnaFayez",
    component: AnaFayez
  },
  {
    path: "/Portfolio/DrsWorld",
    name: "DrsWorld",
    component: DrsWorld
  },
  {
    path: "/Portfolio/Moaaen",
    name: "Moaaen",
    component: Moaaen
  },
  {
    path: "/Portfolio/CerStars",
    name: "CerStars",
    component: CerStars
  },
  {
    path: "/Portfolio/Ketabak",
    name: "Ketabak",
    component: Ketabak
  },
  {
    path: "/Portfolio/Medad",
    name: "Medad",
    component: Medad
  },
  {
    path: "/Portfolio/Merjan",
    name: "Merjan",
    component: Merjan
  },
  {
    path: "/Portfolio/NTS",
    name: "NTS",
    component: NTS
  },
  {
    path: "/Portfolio/Salam",
    name: "Salam",
    component: Salam
  },
  {
    path: "/Portfolio/Takkat",
    name: "Takkat",
    component: Takkat
  },
  {
    path: "/Portfolio/MedadApp",
    name: "MedadApp",
    component: MedadApp
  },
  {
    path: "/Portfolio/DrsWorldApp",
    name: "DrsWorldApp",
    component: DrsWorldApp
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    document.getElementById('app').scrollIntoView();
  }
});

router.beforeResolve((to, from, next) => {
  if (to.path) {
    NProgress.start()
  }
  next()
});

router.afterEach(() => {
  NProgress.done()
});

export default router;
