<template>
  <main @click="closeMenu">
    <section class="project-details">
      <!-- Section Header -->
      <div class="section-header uk-container uk-container-small">
        <h1>
          <span>Project</span> Details
        </h1>
        <p>
          <span uk-icon="icon: chevron-left; ratio: 1.1"></span>
          MORE DETAILS ABOUT THE PROJECT.
          <span uk-icon="icon: chevron-right; ratio: 1.1"></span>
        </p>
      </div>

      <!-- Project Info -->
      <div class="pr-info">
        <div class="uk-container uk-container-small">
          <div class="uk-child-width-1-1" uk-grid>
            <div class="uk-width-1-3@s">
              <img src="@/assets/imgs/works/Salam/logo.png">
            </div>
            <div class="uk-width-2-3@s pr-abstract">
              <div>
                <h3>Salam</h3>
                <p>
                  Salam is a management system.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Project Gallery -->
      <div class="gallery">
        <div class="uk-container uk-container-small">
          <h3>
            <span uk-icon="icon: info; ratio: 0.8"></span>
            Some screens from the dashboard.
          </h3>
          <div class="uk-child-width-1-2 uk-child-width-1-3@m uk-text-center" uk-grid uk-lightbox="animation: slide">
            <div v-for="img in imgs" :key="img.id">
              <div class="uk-card uk-card-default uk-card-body uk-padding-remove">
                <a :href="img.src">
                  <img :src="img.src">
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>

    </section>
  </main>
</template>

<script>
  export default {
    name: "Salam",
    methods: {
      closeMenu (e) {
        var menuBar = document.querySelector('.menu-bar');
        if(e.target.className !== "toggle-btn") {
          menuBar.setAttribute("hidden", "hidden")
        }
      }
    },
    data: function () {
      return {
        imgs: [
          {
            id: 1,
            src: require("@/assets/imgs/works/Salam/Web/1.png")
          },
          {
            id: 2,
            src: require("@/assets/imgs/works/Salam/Web/2.png")
          },
          {
            id: 3,
            src: require("@/assets/imgs/works/Salam/Web/3.png")
          },
          {
            id: 4,
            src: require("@/assets/imgs/works/Salam/Web/4.png")
          },
          {
            id: 5,
            src: require("@/assets/imgs/works/Salam/Web/5.png")
          }
        ]
      }
    }
  }
</script>