<template>
  <!-- App Header -->
  <header>
    <button class="toggle-btn" type="button" uk-toggle="target: #menu-bar; animation:  uk-animation-slide-top-small, uk-animation-slide-top-small">
      <span uk-icon="menu"></span>
    </button>

    <div id="menu-bar" class="menu-bar" hidden>
      <ul>
        <router-link to="/" tag="li" class="menu-item active" @click.native="closeMenu()">
          <a class="menu-link">
            <span uk-icon="home"></span>
            <span class="page-title">Home</span>
          </a>
        </router-link>
        <router-link to="/About" tag="li" class="menu-item" @click.native="closeMenu()">
          <a class="menu-link">
            <span uk-icon="info"></span>
            <span class="page-title">About Me</span>
          </a>
        </router-link>
        <router-link to="/Portfolio" tag="li" class="menu-item" @click.native="closeMenu()">
          <a class="menu-link">
            <span uk-icon="thumbnails"></span>
            <span class="page-title">Portfolio</span>
          </a>
        </router-link>
      </ul>
    </div>
  </header>
</template>

<script>
  export default {
    name: "AppHeader",
    methods: {
      closeMenu () {
        var menuBar = document.querySelector('.menu-bar');
        menuBar.setAttribute("hidden", "hidden")
      }
    }
  }
</script>